<template>
    <header :style="setheaderbg()">
        <div  class="container">
            <div class="row clearfix">
              <div class="col-xs-3">
                  fdsfsdfds
              </div>
               <div class="col-xs-3 col-xs-offset-6">
                   <div class="input-group">
                        <input type="text" class="form-control" v-model="searchKey" placeholder="找我搜品牌...">
                        <span class="input-group-btn">
                            <button class="btn btn-default" type="button" @click=" searchCar"><i class="glyphicon glyphicon-search"></i>搜索</button>
                        </span>
                    </div><!-- /input-group -->
              </div>
            </div>
        </div>     
    </header>   
</template>

<script>
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {   
    data:function(){
        return {
            searchKey:"",
            bgUrl:''       
        }
    },
    methods:{
        getbg:function(){
            let _this = this;
            $.ajax({
                url:PC_COMMONURL+'/common',
                type:"get",
                data:{
                    platForm :'020003'
                },
                dataType:"json",
                asnyc:true,
                success:function(data){
                    if(data.code == SUCCESS_CODE){
                        _this.bgUrl = data.data.storeImage;
                        console.log(data);
                    }               
                }
            })
        },
        setheaderbg:function(){
            return  {
                backgroundImage: "url(" +this.bgUrl + ")",
                backgroundRepeat: "no-repeat",
                backgroundSize: "100% 100%",
          }
        },
        searchCar:function(){
            if(this.searchKey == ""){
                return;
            }else{
                this.$router.push({
                name: 'leaseCar',
                params: {
                    key: this.searchKey
                    }
                })
                $(".nav li").removeClass("active");
                $(".nav li").eq(1).addClass("active");
            }
        }
    },
    mounted:function(){
        this.getbg();
    }
}
</script>



