<template>
<nav class="nav">
    <div class="container">
        <div class="row">
            <div class="col-xs-12">               
                <ul class="nav navbar-nav">
                    <li :class="{'active':0 ==checkindex }" @click="navClick(0)"><router-link to='/'><span>首页</span></router-link></li>
                    <li :class="{'active':1==checkindex }" @click="navClick(1)"><router-link to='/leaseCar'><span>惠搜车</span></router-link></li>
                    <li :class="{'active':2 ==checkindex }" @click="navClick(2)"><router-link to='/importCar'><span>平行进口</span></router-link></li>
                    <li :class="{'active':3 ==checkindex }" @click="navClick(3)"><router-link to='/secondHandCar'><span>会淘车</span></router-link></li>
                    <li :class="{'active':4 ==checkindex }" @click="navClick(4)"><router-link to='/loan'><span>惠鑫贷</span></router-link></li>
                    <li :class="{'active':5 ==checkindex }" @click="navClick(5)"><router-link to='/'><span>惠车邦</span></router-link></li>
                    <li :class="{'active':6 ==checkindex }" @click="navClick(6)"><router-link to='/information'><span>资讯</span></router-link></li>
                    <li :class="{'active':7 ==checkindex }" @click="navClick(7)"><router-link to='/aboutUs'><span>关于我们</span></router-link></li>
                </ul>                 
            </div>
        </div>
    </div> 
    </nav>      
</template>

<script>
export default {
    data:function(){
        return{
            checkindex: 0 
        }
    },
    methods:{
        navClick:function(index){
            $('.nav li').removeClass("active");
            this.checkindex = index;
        }
    }
}
</script>



